import { InMemoryDbService } from 'angular-in-memory-web-api';
import { Badge} from './badge';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class InMemoryDataService implements InMemoryDbService {
  createDb() {
    const badges = [
      {id: 1, name: 'Water'},
      {id: 2, name: 'Waste'},
      {id: 3, name: 'Academics'}, 
      {id: 4, name: 'Engagement'},
      {id: 5, name: 'Climate'},
      {id: 6, name: 'Landscaping'},
      {id: 7, name: 'Purchasing'},
      {id: 8, name: 'Transportation'},
      {id: 9, name: 'Energy'},
      {id: 10, name: 'Climate Commitment'}
    ];
    return {badges};
  }

  // Overrides the genId method to ensure that a badge always has an id.
  // If the badges array is empty,
  // the method below returns the initial number (11).
  // if the badges array is not empty, the method below returns the highest
  // badge id + 1.
  genId(badges: Badge[]): number {
    return badges.length > 0 ? Math.max(...badges.map(badge => badge.id)) + 1 : 11;
  }
}