import { Component, OnInit } from '@angular/core';

import { Observable, Subject } from 'rxjs';

import {
   debounceTime, distinctUntilChanged, switchMap
 } from 'rxjs/operators';

import { Badge } from '../badge';
import { BadgeService } from '../badge.service';

@Component({
  selector: 'app-badge-search',
  templateUrl: './badge-search.component.html',
  styleUrls: [ './badge-search.component.css' ]
})
export class BadgeSearchComponent implements OnInit {
  badges$: Observable<Badge[]>;
  private searchTerms = new Subject<string>();

  constructor(private badgeService: BadgeService) {}

  // Push a search term into the observable stream.
  search(term: string): void {
    this.searchTerms.next(term);
  }

  ngOnInit(): void {
    this.badges$ = this.searchTerms.pipe(
      // wait 300ms after each keystroke before considering the term
      debounceTime(300),

      // ignore new term if same as previous term
      distinctUntilChanged(),

      // switch to new search observable each time the term changes
      switchMap((term: string) => this.badgeService.searchBadges(term)),
    );
  }
}