import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { Badge }         from '../badge';
import { BadgeService }  from '../badge.service';

@Component({
  selector: 'app-badge-detail',
  templateUrl: './badge-detail.component.html',
  styleUrls: [ './badge-detail.component.css' ]
})
export class BadgeDetailComponent implements OnInit {
  @Input() badge: Badge;

  constructor(
    private route: ActivatedRoute,
    private badgeService: BadgeService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.getBadge();
  }

  getBadge(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    this.badgeService.getBadge(id)
      .subscribe(badge => this.badge = badge);
  }

  goBack(): void {
    this.location.back();
  }

 save(): void {
    this.badgeService.updateBadge(this.badge)
      .subscribe(() => this.goBack());
  }
}