import { Component, OnInit } from '@angular/core';

import { Badge } from '../badge';
import { BadgeService } from '../badge.service';

@Component({
  selector: 'app-badges',
  templateUrl: './badges.component.html',
  styleUrls: ['./badges.component.css']
})
export class BadgesComponent implements OnInit {
  badges: Badge[];

  constructor(private badgeService: BadgeService) { }

  ngOnInit() {
    this.getBadges();
  }

  getBadges(): void {
    this.badgeService.getBadges()
    .subscribe(badges => this.badges = badges);
  }

  add(name: string): void {
    name = name.trim();
    if (!name) { return; }
    this.badgeService.addBadge({ name } as Badge)
      .subscribe(badge => {
        this.badges.push(badge);
      });
  }

  delete(badge: Badge): void {
    this.badges = this.badges.filter(h => h !== badge);
    this.badgeService.deleteBadge(badge).subscribe();
  }

}