import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { Badge } from './badge';
import { MessageService } from './message.service';


@Injectable({ providedIn: 'root' })
export class BadgeService {

  private badgesUrl = 'api/badges';  // URL to web api

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(
    private http: HttpClient,
    private messageService: MessageService) { }

  /** GET badges from the server */
  getBadges (): Observable<Badge[]> {
    return this.http.get<Badge[]>(this.badgesUrl)
      .pipe(
        tap(_ => this.log('fetched badges')),
        catchError(this.handleError<Badge[]>('getBadges', []))
      );
  }

  /** GET badge by id. Return `undefined` when id not found */
  getBadgeNo404<Data>(id: number): Observable<Badge> {
    const url = `${this.badgesUrl}/?id=${id}`;
    return this.http.get<Badge[]>(url)
      .pipe(
        map(badges => badges[0]), // returns a {0|1} element array
        tap(h => {
          const outcome = h ? `fetched` : `did not find`;
          this.log(`${outcome} badge id=${id}`);
        }),
        catchError(this.handleError<Badge>(`getBadge id=${id}`))
      );
  }

  /** GET badge by id. Will 404 if id not found */
  getBadge(id: number): Observable<Badge> {
    const url = `${this.badgesUrl}/${id}`;
    return this.http.get<Badge>(url).pipe(
      tap(_ => this.log(`fetched badge id=${id}`)),
      catchError(this.handleError<Badge>(`getBadge id=${id}`))
    );
  }

  /* GET badges whose name contains search term */
  searchBadges(term: string): Observable<Badge[]> {
    if (!term.trim()) {
      // if not search term, return empty badge array.
      return of([]);
    }
    return this.http.get<Badge[]>(`${this.badgesUrl}/?name=${term}`).pipe(
      tap(x => x.length ?
         this.log(`found badges matching "${term}"`) :
         this.log(`no badges matching "${term}"`)),
      catchError(this.handleError<Badge[]>('searchBadges', []))
    );
  }

  //////// Save methods //////////

  /** POST: add a new badge to the server */
  addBadge (badge: Badge): Observable<Badge> {
    return this.http.post<Badge>(this.badgesUrl, badge, this.httpOptions).pipe(
      tap((newBadge: Badge) => this.log(`added badge w/ id=${newBadge.id}`)),
      catchError(this.handleError<Badge>('addBadge'))
    );
  }

  /** DELETE: delete the badge from the server */
  deleteBadge (badge: Badge | number): Observable<Badge> {
    const id = typeof badge === 'number' ? badge : badge.id;
    const url = `${this.badgesUrl}/${id}`;

    return this.http.delete<Badge>(url, this.httpOptions).pipe(
      tap(_ => this.log(`deleted badge id=${id}`)),
      catchError(this.handleError<Badge>('deleteBadge'))
    );
  }

  /** PUT: update the badge on the server */
  updateBadge (badge: Badge): Observable<any> {
    return this.http.put(this.badgesUrl, badge, this.httpOptions).pipe(
      tap(_ => this.log(`updated badge id=${badge.id}`)),
      catchError(this.handleError<any>('updateBadge'))
    );
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a BadgeService message with the MessageService */
  private log(message: string) {
    this.messageService.add(`BadgeService: ${message}`);
  }
}